import type { MetricCounts, MetricGauges } from '@zg-rentals/pontoon';
import type { Request } from 'express';

const TIME_THESHOLD_MS = 1000 * 60 * 10;
const BATCH_SIZE_THRESHOLD = 1000;
const REQUIRED_HEADERS = ['host', 'referer', 'accept'];

function urlsMatch(hostname: string, headerUrl?: string): boolean {
  if (headerUrl !== undefined) {
    headerUrl = headerUrl.toLowerCase();
    hostname = hostname.toLowerCase();
    return headerUrl.includes(hostname) || hostname.includes(headerUrl);
  }
  return false;
}

export function validateHeaders(req: Request, hostname?: string): boolean {
  const headers = req.headers;
  const isCorsRequest = hostname && (!urlsMatch(hostname, headers.host) || !urlsMatch(hostname, headers.referer));
  if (isCorsRequest || REQUIRED_HEADERS.find((header) => headers[header] === undefined)) {
    return false;
  }
  return true;
}

export function validateCounts({ counts, browserTime }: { counts: MetricCounts['counts']; browserTime: number }) {
  let minDate = Infinity,
    maxDate = -Infinity;
  const now = Date.now();
  const browserClockOffset = now - browserTime;
  const correctedCounts: MetricCounts['counts'] = {};
  const timestamps = Object.keys(counts);
  if (timestamps.length > BATCH_SIZE_THRESHOLD) {
    return false;
  }
  const foundInvalidData = timestamps.find((timestamp) => {
    if (Object.keys(counts[timestamp]).length > BATCH_SIZE_THRESHOLD) {
      return true;
    }
    const epochMs = Number(timestamp);
    if (isNaN(epochMs)) {
      return true;
    }
    const correctedTime = epochMs + browserClockOffset;
    if (Math.abs(now - correctedTime) > TIME_THESHOLD_MS) {
      return true;
    }
    minDate = Math.min(minDate, epochMs);
    maxDate = Math.max(maxDate, epochMs);
    correctedCounts[correctedTime] = counts[timestamp];
  });
  if (foundInvalidData || maxDate - minDate > TIME_THESHOLD_MS) {
    return false;
  }
  return correctedCounts;
}

export function validateGauges({ gauges, browserTime }: { gauges: MetricGauges['gauges']; browserTime: number }) {
  let minDate = Infinity,
    maxDate = -Infinity;
  const now = Date.now();
  const browserClockOffset = now - browserTime;
  const gaugeKeys = Object.keys(gauges);
  if (gaugeKeys.length > BATCH_SIZE_THRESHOLD) {
    return false;
  }
  const foundInvalidData = gaugeKeys.find((gaugeKey) => {
    return gauges[gaugeKey].find((dataPoint) => {
      const epochMs = Number(dataPoint[0]);
      dataPoint[1] = Number(dataPoint[1]);
      if (isNaN(epochMs) || isNaN(dataPoint[1])) {
        return true;
      }
      const correctedTime = epochMs + browserClockOffset;
      if (Math.abs(now - correctedTime) > TIME_THESHOLD_MS) {
        return true;
      }
      minDate = Math.min(minDate, epochMs);
      maxDate = Math.max(maxDate, epochMs);
      dataPoint[0] = correctedTime;
    });
  });
  if (foundInvalidData || maxDate - minDate > TIME_THESHOLD_MS) {
    return false;
  }
  return gauges;
}
