import type { Application, NextFunction, Request, Response } from 'express';
import { Pontoon } from '@zg-rentals/pontoon';
import express from 'express';
import type { Logger } from '@zg-rentals/particles-js-base-logger';
import { validateCounts, validateGauges, validateHeaders } from './validate';

interface PontoonProxyOptions {
  api: string;
  apiKey: string;
  app: Application;
  hostname?: string;
  logger?: Logger;
  proxyPath?: string;
  serverName: string;
  serviceName: string;
}

const PONTOON_PATH_PREFIX = 'pontoon/api/';
const REQUIRED_OPTIONS = ['api', 'apiKey', 'app', 'serverName', 'serviceName'];

export function createPontoonProxy(options: PontoonProxyOptions) {
  const missingOptions = REQUIRED_OPTIONS.filter((opt) => !options[opt as keyof PontoonProxyOptions]);
  if (missingOptions.length) {
    throw new Error(`Cannot proxy pontoon requests without required options: ${missingOptions.join(', ')}`);
  }

  const { api, apiKey, app, hostname, logger, serverName, serviceName } = options;

  let { proxyPath = '/proxy/' } = options;

  if (proxyPath[0] !== '/') {
    proxyPath = `/${proxyPath}`;
  }
  if (proxyPath[proxyPath.length - 1] !== '/') {
    proxyPath = `${proxyPath}/`;
  }

  const fullProxyPath = proxyPath + PONTOON_PATH_PREFIX;

  const pontoon = new Pontoon({ api, apiKey });

  app.use(`${fullProxyPath}*`, express.json(), async (req: Request, res: Response, next: NextFunction) => {
    if (req.method !== 'POST' || !validateHeaders(req, hostname)) {
      return next();
    }
    const pontoonPath = req.originalUrl.replace(fullProxyPath, '');
    let pontoonRequest;

    if (pontoonPath === 'countPublisher/v2/add') {
      const validatedCounts = validateCounts(req.body);
      if (validatedCounts) {
        pontoonRequest = pontoon.sendCounts({
          serverName,
          serviceName,
          counts: validatedCounts,
        });
      } else {
        logger?.error('invalid request to proxy pontoon counts');
        return res.status(400).end();
      }
    } else if (pontoonPath === 'gaugePublisher/v2/add') {
      const validatedGauges = validateGauges(req.body);
      if (validatedGauges) {
        pontoonRequest = pontoon.sendGauges({
          serverName,
          serviceName,
          gauges: validatedGauges,
        });
      } else {
        logger?.error('invalid request to proxy pontoon gauges');
        return res.status(400).end();
      }
    } else {
      return next();
    }
    logger?.info(`proxying ${pontoonPath}`);
    await pontoonRequest
      .then(() => res.end())
      .catch((err) => {
        logger && logger.error(err);
        res.end(500);
      });
  });
}
