import { createPontoonProxy as zgRentalsCreatePontoonProxy } from '@zg-rentals/pontoon-proxy';
import log from '../../server/utils/log';
import type { Logger } from '@zg-rentals/particles-js-base-logger';
import type { Express } from 'express-serve-static-core';

const pontoonApi = process.env.PONTOON_API as string;
const pontoonApiKey = process.env.PONTOON_API_KEY as string;
const serviceName = process.env.APP_NAME as string;
const zgEnv = process.env.ZG_ENV;
const serverName = 'client';
const shermanName = 'sherman';

const pontoonLogger = log.child('pontoon proxy') as Logger;

export default function createPontoonProxy(app: Express) {
  try {
    zgRentalsCreatePontoonProxy({
      app,
      api: pontoonApi,
      apiKey: pontoonApiKey,
      serverName,
      serviceName,
      logger: pontoonLogger,
    });
  } catch (e) {
    if (__DEV__ || zgEnv === shermanName) {
      log.warn(e.message);
    } else {
      throw e;
    }
  }
}
